import React from 'react';
import Plot from 'react-plotly.js';

import chartBase from '../../Utility/chartBase';

const SparseScatter = (props) => {
    const {
        xValues,
        variableValues,
        markerOptions,
        infoObject,
        xTitle
    } = props;

    const { parameters, metadata } = infoObject;

    return (
        <Plot
            style= {{
                position: 'relative',
                display:'inline-block'
            }}
    
            data={[
                {
                    x: xValues,
                    y: variableValues,
                    // error_y: {
                    // type: 'data',
                    // array: stds,
                    // opacity: 0.2,
                    // color: 'gray',
                    // visible: true
                    // },
                    mode: 'markers',
                    name: parameters.fields,
                    type: variableValues.length > 1000 ? 'scattergl' : 'scatter',
                    // line: {color: markerOptions.color},
                    marker: {
                        opacity: markerOptions.opacity,
                        size: markerOptions.size,
                        color: markerOptions.color
                    }
                },
            ]}
            config={{...chartBase.config}}
            layout= {{
                ...chartBase.layout,
                width: 800,
                height: 570,
                title: `${parameters.fields}[${metadata.Unit}]  ${parameters.dt1} to ${parameters.dt2}`,
                xaxis: {
                    title: xTitle,
                    color: '#ffffff',
                    exponentformat: 'power'
                },
                yaxis: {
                    title: parameters.fields,
                    color: '#ffffff',
                    exponentformat: 'power'
                },
                annotations: chartBase.annotations(metadata.Distributor)
            }}            
        />
    )
}

export default SparseScatter;